.move-list{
    h1{
        text-decoration: underline;
        text-underline-offset: 6px;
    }
    ul{
        li{
            border-bottom: 1px solid orange;
            padding-bottom: 6px;
            margin-bottom: 10px;
        }
        padding: 0;
        list-style-type: none;
    }
    padding: 10px 20px 20px 20px;
    max-width: 400px;
    border-radius: 8px;
    margin: 30px auto;
    
  display: flex;
  gap: 15px;
  flex-direction: column;
  border: 1px solid  #f7951e;
  text-align: center;
}