.contact-list {
    grid-auto-rows: minmax(200px, auto);
    ul{
        padding: 0;
        list-style-type: none;
    }
}

.contacts-list{
    .info{
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 20px;
        img{
            height: 100px;
            width: 125px;
        }
    }
    ul{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        gap: 15px;
        padding: 0;
        list-style: none;
    }
}
